var result = new Object();

result.appText = uni.getStorageSync("i18n:appText");

result.setAppText = function(data) {
	return uni.setStorageSync("i18n:appText",data);
}

result.getAppText = function() {
	return uni.getStorageSync("i18n:appText");
}

result.getAppTextByCode = function(code) {

	if(code == null || code == ""){
		return "xxx CODE xxx";
	}


	var appText = result.getAppText();
	if (appText == null) {
		return "xxx "+code +" xxx";
	}

	// 通过.分割code，appText[code]可能是一个对象，也可能是一个字符串,如果是对象，就继续取值
	var codeArray = code.split(".");
	var ret = appText;
	for (var i = 0; i < codeArray.length; i++) {
		var code1 = codeArray[i];
		if (ret[code1] == null) {
			return "xxx "+code +" xxx";
		}
		ret = ret[code1];
	}
	return ret;

}

result.getLanguage = function() {
	if (uni.getStorageSync("languageCode") != null && uni.getStorageSync("languageCode") != "") {
		return uni.getStorageSync("languageCode");
	}
	return (uni.getSystemInfoSync().language+"").replace("_","-");
}

result.language = result.getLanguage();

export default result;
